
<van-field
  :class="$bem({ disabled })"
  right-icon="arrow"
  v-bind="$attrs"
  :disabled="disabled"
  @click-input="handleClickInput"
  @click-right-icon="handleClickInput"
  ref="fieldRef"
>
  <template #input>
    <mb-tags
      :$bem="$bem"
      :$translate="$translate"
      v-bind="tagsProps"
      v-model="bindValue"
      @close-tag="onCloseTag"
    />
  </template>
  <template #extra>
    <mb-popup
      :style="popupStyle"
      :class="popupClass"
      v-model="showPopup"
      v-bind="mergePopupProps"
      v-on="popupEvents"
      @sure="onSure"
      @close="onClose"
      @search="onSearch"
    >
      <slot />
    </mb-popup>
  </template>
</van-field>
