
<div :class="$bem('tags', { disabled })">
  <template v-if="clearable || collapse">
    <template v-if="collapse">
      <div v-if="tags.length">
        <van-tag
          v-for="(tag, index) in collapseTags"
          :key="index"
          :closeable="clearable"
          :size="tagSize"
          plain
          @close="close(tag)"
        >
          {{ tag[labelKey] }}
        </van-tag>
        <van-tag
          v-if="tags.length - collapseCount >= 1"
          :size="tagSize"
          plain
        >
          + {{ tags.length - collapseCount }}
        </van-tag>
      </div>
      <span v-else :class="$bem('tags--placeholder')">
        {{ $translate('common.select') }}
      </span>
    </template>

    <template v-else>
      <div v-if="tags.length">
        <van-tag
          v-for="(tag, index) in tags"
          :key="index"
          :closeable="clearable"
          :size="tagSize"
          plain
          @close="close(tag)"
        >
          {{ tag[labelKey] }}
        </van-tag>
      </div>
      <span v-else :class="$bem('tags--placeholder')">
        {{ $translate('common.select') }}
      </span>
    </template>
  </template>
  <span v-else :class="labels ? '' : $bem('tags--placeholder')">
    {{ labels || $translate('common.select') }}
  </span>
</div>
